import React from 'react'
import { useState } from 'react';
import { useEffect } from 'react';


const Search_API = "https://api.themoviedb.org/3/search/movie?api_key=a20e52f6f51cafc80ee323856e0e6623&query="
export default function Header(props) {

    const [search, setSearch] = useState('');
    const handleQuery = (e) => {
        e.preventDefault();

        async function showResults() {
            const Query_response = await (await fetch(Search_API + search)).json();
            console.log(Query_response)
            { props.onUpdate(Query_response.results) }
        }
        showResults();
    }

    const updateQuery =async (e) => {
        setSearch(e.target.value);
        setTimeout(async() => {
            const Query_response = await (await fetch(Search_API + search)).json();
            console.log(Query_response)
            { props.onUpdate(Query_response.results) }
          }, 2000);
    }
    
    return (
        <div>
            <nav className="navbar navbar-expand-lg" style={{ backgroundColor: "#151926" }}>
                <div className="container-fluid">
                    <a className="navbar-brand text-white">Thunder Movies</a>

                    <button
                        className="navbar-toggler"
                        type="button"
                        data-mdb-toggle="collapse"
                        data-mdb-target="#navbarSupportedContent"
                        aria-controls="navbarSupportedContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <i className="fas fa-bars text-white"></i>
                    </button>

                    <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
                        <div className="d-flex">
                            <ul className="navbar-nav d-flex flex-row me-1" style={{ marginTop: "-4px" }}>
                                <li className="nav-item me-3 me-lg-0">
                                    <a onClick={()=>{props.setFav()}} className="nav-link text-white" href="#"><i className="fas fa-bookmark"></i></a>
                                </li>
                            </ul>

                            <form onSubmit={handleQuery} className="w-100 d-inline-block">
                                <input
                                    type="search"
                                    className="form-control"
                                    placeholder="Type query"
                                    aria-label="Search"
                                    value={search}
                                    onChange={updateQuery}
                                />
                            </form>
                        </div>
                    </div>
                </div>

            </nav>
        </div>
    )
}
