import React from 'react'

const Thumbnail_API = "https://image.tmdb.org/t/p/w400";

const styles_outer_div = {
    backgroundColor: '#151926',
    border: '1px solid black',
    maxWidth: "350px",
    maxHeight: "500px",
}
const style_mask = {
    backgroundColor: 'rgba(52, 52, 52, 0.8)',
}

const style_movie_description_box = {
    maxHeight: "100px",
    overflow: "hidden",
    textOverflow: "ellipsis"
}
export default function Movies(props) {
    return (
        <div className="d-inline-block">
            <div className="card m-2 animate__animated animate__fadeIn" style={styles_outer_div} >
                <div className="bg-image hover-overlay ripple" data-mdb-ripple-color="light">
                    <img src={Thumbnail_API + props.movie.backdrop_path} alt={props.movie.title} className="img-fluid" />


                    <a href="#!">
                        <div className="mask" style={style_mask}>
                            <div className="d-flex justify-content-center align-items-center h-100">
                                <p className="text-white mb-0">{props.movie.title}</p>
                            </div>
                        </div>
                    </a>
                </div>
                <div className="card-body" style={{ backgroundColor: '#151926' }}>
                    <h5 className="card-title text-white">{props.movie.title}</h5>
                    <p className="card-text text-white" style={style_movie_description_box} title={props.movie.overview}>
                        {props.movie.overview}
                    </p>
                    <a href="#!" onClick={() => { props.addMovie(props.movie) }} className="btn btn-primary"><i className="fas fa-bookmark"></i></a>

                    {props.movie.delete === 'yep' ? 
                        <a href="#!" onClick={()=>{props.deleteMovie(props.movie)}} className="btn btn-danger mx-4 text-white "><i class="fa fa-trash" aria-hidden="true"></i></a> 
                    :
                        ""
                    }

                </div>
                <div className="card-footer text-muted text-center">
                    {props.movie.release_date}
                    <span className="badge bg-primary ms-2" >{props.movie.vote_count} votes</span>
                    <span className="badge bg-danger ms-2">{props.movie.vote_average} / 10</span>
                </div>
            </div>

        </div>
    )
}
